import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	components: {
		uniSwipeAction,
		uniList,
		uniListItem
	},
	data() {
		return {
			selectYear: '',
			selectMonth: '',
			selectDate: '',
			listData: [{
				time: '2019-01-02',
				data: [{
					text: '取消置顶'
				}, {
					text: '取消置顶'
				}, {
					text: '取消置顶'
				}, {
					text: '取消置顶'
				}]
			}, {
				time: '2019-01-03',
				data: [{
					text: '取消置顶'
				}, {
					text: '取消置顶'
				}, {
					text: '取消置顶'
				}, {
